import Line from "../Line/Line";
import Lunches from "../Lunches/Lunches";

const Lunch = () => {
  return (
    <div className="lunch">
      <h1 className="lunch__title"></h1>
      <div className="lunch__block-time">
        <p className="lunch__time"> - </p>
        <p className="lunch__time">  12:00  16:00</p>
      </div>
      <div className="lunch__block-price">
        <p className="lunch__price">  3  + </p>
        <span className="lunch__price">350 .</span>
      </div>
      <Line />
      <Lunches
        day=""
        soup=" "
        main="   "
        salad=" "
        drink=" /  / -"
      />
      <Lunches
        day=""
        soup=" "
        main="    "
        salad=" "
        drink=" /  / -"
      />
      <Lunches
        day=""
        soup="  "
        main="    "
        salad=" "
        drink=" /  / -"
      />
      <Lunches
        day=""
        soup=""
        main="  "
        salad=" "
        drink=" /  / -"
      />
      <Lunches
        day=""
        soup="     "
        main="     "
        salad=" -"
        drink=" /  / -"
      />
    </div>
  );
};

export default Lunch;
